.message-history {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.message-history:nth-child(even) {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.message {
  padding: 1rem;
  width: 80%;
  border: 1pc solid orange;
  box-shadow: 0px 0px 9px red;
  border-radius: 1rem;
}
